import React, { Component } from 'react'
import {Link, Route} from 'react-router-dom'
import All from '../second/All'
import Jinghua from '../second/Jinghua'
import Share from '../second/Share'
import Wenda from '../second/Wenda'
import Zhaopin from '../second/Zhaopin'
import Test from '../second/Test'
import Detils from '../third/Detils'
import Detils1 from '../third/Detils1'
import $ from 'jquery'
export default class Home extends Component {
    
    render() {
        // let {url}= this.props.match;
        // console.log(this.props.match);
        return (
            <div>
                <div className='nav'>
                    <div style={{float:'left',color:'#cccccc',paddingTop:10}} >
                        <Link to='/home/all'>全部</Link>
                        <Link to='/home/jinghua'>精华</Link>
                        <Link to='/home/share'>分享</Link>
                        <Link to='/home/wenda'>问答</Link>
                        <Link to='/home/zhaopin'>招聘</Link>
                        <Link to='/home/test'>客户端测试</Link>  
                        <Link to='/home/kong/:id'></Link>  
                       
                    </div>
                    <br/>
                    <div style={{paddingTop:20}}>
                        <Route path='/home/all' component={All}/>
                        <Route path='/home/jinghua' component={Jinghua}/>
                        <Route path='/home/share' component={Share}/>
                        <Route path='/home/wenda' component={Wenda}/>
                        <Route path='/home/zhaopin' component={Zhaopin}/>
                        <Route path='/home/test' component={Test}/>
                        <Route path='/home/kong/:id' component={Detils}/>
                        
                    </div>    
                </div>
            </div>
            
        )
    }
}
