import React, { Component } from 'react'
import {Link, Route} from 'react-router-dom'
import $ from 'jquery'

export default class Jinghua extends Component {
    constructor() {
        super();
        this.state = {
            data: []
        }
    }
    componentDidMount() {
        fetch('https://cnodejs.org/api/v1/topics?tab=good&page=1')
            .then((res)=>res.json())
            .then((res)=>{
                console.log(res)
                this.setState({
                    data: res.data
                });
            })
    }
    componentDidUpdate(prevProps,prevState){
        if(this.state.id !== prevState.id){
            fetch('https://cnodejs.org/api/v1/topics?tab=good&page='+this.state.id)
            .then((res)=>res.json())
            .then((res)=>{
               
                this.setState({
                    data: res.data
                });
            })
        }
    }
    changeValue1 = ()=>{
       var Id = $("#a1").html();
        this.setState({
            id:Id
        })
    }
    changeValue2 = ()=>{
        var Id = $("#a2").html();
         this.setState({
             id:Id
         })
     }
     changeValue3 = ()=>{
        var Id = $("#a3").html();
         this.setState({
             id:Id
         })
     }
     changeValue4 = ()=>{
        var Id = $("#a4").html();
         this.setState({
             id:Id
         })
     }
     changeValue5 = ()=>{
        var Id = $("#a5").html();
         this.setState({
             id:Id
         })
     }
     changeValue6 = ()=>{
        var Id = $("#a6").html();
         this.setState({
             id:Id
         })
     }
     changeValue7 = ()=>{
        var Id = $("#a7").html();
         this.setState({
             id:Id
         })
     }
     changeValue8 = ()=>{
        var Id = $("#a8").html();
         this.setState({
             id:Id
         })
     }
     changeValue9 = ()=>{
        var Id = $("#a9").html();
         this.setState({
             id:Id
         })
     }
     changeValue0 = ()=>{
        var Id = $("#a0").html();
         this.setState({
             id:Id
         })
     }
    render() {
        return (
            <div>
                 {
                    this.state.data.map((item) => (
                        <li className='cell'>
                            <img style={{width:30,height:30}} src={item.author['avatar_url']} />
                            <span style={{color:'#9e78c0',fontSize:14,paddingLeft:15}}>{item.reply_count}/</span>
                            <span style={{fontSize: 10,color: '#b4b4b4'}}>{item.visit_count}</span>
                            <Link to={'/home/kong/'+item.id} style={{color:'blcak',fontSize: 16,paddingLeft:10}}>
                                {item.title}
                            </Link>
                            <span style={{float:'right',paddingRight:20,paddingTop:10,color: '#778087',fontSize:11}}>2天前</span>
                            <img style={{width:18,height:18,float:'right',paddingRight:20,paddingTop:10}} src="https://avatars3.githubusercontent.com/u/56661717?v=4&amp;s=120"></img>
                            
                        </li>
                    ))
    
                }
                <div>
                    <ul className='num'   >
                        <li id='a1' onClick={this.changeValue1}>1</li>
                        <li id='a2' onClick={this.changeValue2} >2</li>
                        <li id='a3' onClick={this.changeValue3}>3</li>
                        <li id='a4' onClick={this.changeValue4}>4</li>
                        <li id='a5' onClick={this.changeValue5}>5</li>
                        <li id='a6' onClick={this.changeValue6}>6</li>
                        <li id='a7' onClick={this.changeValue7}>7</li>
                        <li id='a8' onClick={this.changeValue8}>8</li>
                        <li id='a9' onClick={this.changeValue9}>9</li>
                        <li id='a0' onClick={this.changeValue0}>10</li>
                    </ul>
                </div>
            </div>
        )
    }
}
