import React, { Component } from 'react'
import {Link, Route} from 'react-router-dom'
import All from '../second/All'
export default class Signin extends Component {
    render() {
        return (
            <div>
                <div>
                    <Link>主页</Link>
                    <span>/登录</span>
                </div>
                <div style={{marginTop:20,backgroundColor:'white',height:250,borderRadius: '0 0 3px 3px',fontSize:14,textAlign:'center'}}>
                    <span style={{marginTop:50}}>用户名</span>
                    <input style={{marginTop:50,marginLeft:20,size:30}} type="text"/>
                    <br/>
                    <span style={{marginTop:20}}>密码</span>
                    <input style={{marginTop:20,marginLeft:35}} type="password"/>
                    <br/>
                    <Link to="/home/all"><button style={{marginTop:20}}>登录</button></Link>
                </div>
                <div>
                    <Route path='/home/all' compoent={All}/>
                </div>
            </div>
            
        )
    }
}
